:root {
	--clr-gray-light: #d7dfe2;
	--clr-gray-lightmed: #a2a9ab;
	--clr-gray-med: #616b74;
	--clr-gray-dark: #414b56;
	--clr-primary: #2460fc;
}



.services {
  width: 100%;
  background-color: #f8f8f8;
}

.services h2 {
  font-size: 3rem;
  text-align: center;
}

.services .container {
  width: 1240px;
  margin: 4rem auto;
  padding: 4rem 0rem;
}




.wave-top {
	top: 0;
}

.wave-bottom {
	bottom: 0;
}



.wrapper {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}

.card {
	box-shadow: 0px 2px 10px var(--clr-gray-lightmed);
	background: rgb(255, 255, 255);
	border-radius: 0.25rem;
	position: relative;
	min-width: 350px;
	margin: 1rem;
	text-align: center;
	transition: 500ms all ease-in-out;
	cursor: pointer;
}

.card:hover {
	transform: scale(1.02);
	box-shadow: 0px 2px 20px var(--clr-gray-lightmed);
}

.card-banner {
	border-bottom: 1px solid var(--clr-gray-light);
	padding: 4rem 0 2rem;
}

	.plan-tag {
		font-size: 0.8rem;
		font-weight: bold;
		color: var(--clr-gray-dark);
		background: var(--clr-gray-light);
		padding: 0.5rem 1rem;
		text-transform: uppercase;
		position: absolute;
		z-index: 1;
		top: 1rem;
		left: 1rem;
		border-radius: 0.25rem;
	}

	.highlight {
		color: white;
		background: var(--clr-primary);
	}

	.plan-price {
		display: inline-block;
	}

	.price {
		font-size: 4.5rem;
		position: relative;
		margin-bottom: -0.5rem;
		color: var(--clr-gray-dark);
	}

	.price::before {
		content: "$";
		display: inline-block;
		font-size: 2rem;
		top: 0.8rem;
		position: absolute;
		text-align: left;
		left: -1.5rem;
	}

	.price-month {
		font-size: 0.9rem;
	}


.card-body {
	margin: 2rem 0;
}

	.plan-name {
		text-transform: uppercase;
		font-size: 0.9rem;
		letter-spacing: 0.4rem;
		margin-bottom: 1rem;
	}

	.plan-feature-list {
		margin: 2rem 4rem;
		list-style: none;
	}

	.plan-feature {
		font-size: 0.9rem;
		margin: 1.5rem 0;
		text-align: left;
	}

	.plan-feature::before {
		font-family: "Font Awesome 5 Free";
		font-weight: 900;
		font-size: 1.3rem;
		content: "\f058";
		color: var(--clr-gray-lightmed);
		display: inline-block;
		position: relative;
		vertical-align: middle;
		margin-right: 1rem;
	}

.btn {
	width: 250px;
	color: var(--clr-primary);
	padding: 1rem;
	text-transform: uppercase;
	font-weight: bold;
	border: 2px solid var(--clr-primary);
	background: white;
	border-radius: 0.25rem;
	cursor: pointer;
	transition: 500ms all ease-in-out;
}

.card:hover .btn {
	background: var(--clr-primary);
	color: white;
}



@media screen and (max-width: 940px) {
  .services .container {
      width: 100%;
  }

  .services .container .content {
      grid-template-columns: 1fr;
  }

  .services .card {
      max-width: 100%;
      margin: 1rem;
  }
}